@media screen and (min-width: 1280px) {
    .sLoginImage{
        display: inline;
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0px;
        top: 0px;
        background: url(../../images/home.jpg);
        background-size: 100% 100%;
    }
    .sLoginFrame{
        display: absolute;
        position: absolute;
        width: 550px;
        height: 350px;
        left: calc(50% - 275px);
        top: calc(50% - 175px);
        opacity: 0.9;
        background-color: #909497;
        border-radius: 6px;
    }
    .sLoginInputUsr{
        display: absolute;
        position: absolute;
        width: 200px;
    }
}